<template>
  <el-container style="height: 100%">
    <el-header height="100px">
      <span style="font-family: Consolas; font-size: 24px">忘川风华录辨音猜词辅助工具</span>
    </el-header>
    <el-container>
      <el-aside width="220px">
        <div>
          <el-input v-model="input" placeholder="输入已知条件" @keyup.enter.native="add"/>
        </div>
        <div style="margin-top: 15px;text-align: center">
          <el-button type="primary" size="small" plain @click="add">查询</el-button>
          <el-button type="warning" size="small" plain @click="pop">撤销</el-button>
          <el-button type="danger" size="small" plain @click="clear">清除</el-button>
        </div>
        <div style="margin-top: 15px;height: calc(100% - 115px)">
          <el-table :data="query" :show-header="false" stripe height="100%">
            <el-table-column prop="val" align="center"/>
          </el-table>
        </div>
      </el-aside>
      <el-main>
        <el-table :data="result" :show-header="false" stripe height="calc(100% - 1px)">
          <el-table-column prop="val" align="center"/>
        </el-table>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
class dat {
  word;
  data;

  constructor(word, data) {
    this.word = word
    this.data = data;
  }
}

class qry {
  id;
  s;

  constructor(id, s) {
    this.id = id;
    this.s = s;
  }
}

export default {
  methods: {
    add() {
      let id = -1;
      let s = "";
      let text = this.input;
      if (text === "") return;
      if (text === "!") {
        this.clear();
        return;
      }
      if (text === ".") {
        this.pop();
        return;
      }
      let elem = text.split(" ");
      if (!isNaN(Number(elem[0])) && Number(elem[0]) >= 1 && Number(elem[0]) <= 4) {
        id = elem[0] - 1;
        s = elem[1];
      } else {
        s = elem[0];
      }
      this.que.push(new qry(id, s));
      if (this.input !== '') {
        this.query.push({val: this.input});
        this.input = '';
      }
      this.work();
    },
    pop() {
      if (this.query.length > 0) {
        this.query.pop();
        this.que.pop();
      }
      this.work();
    },
    clear() {
      this.query = Array();
      this.que = Array();
      this.work();
    },
    work() {
      if (this.query.length === 0) {
        this.result = Array();
        return;
      }
      const d = [
        new dat("高屋建瓴", [["高", "g", "ao", "-"], ["屋", "w", "u", "-"], ["建", "j", "ian", "\\"], ["瓴", "l", "ing", "/"]]),
        new dat("作壁上观", [["作", "z", "uo", "\\"], ["壁", "b", "i", "\\"], ["上", "sh", "ang", "\\"], ["观", "g", "uan", "-"]]),
        new dat("四面楚歌", [["四", "s", "i", "\\"], ["面", "m", "ian", "\\"], ["楚", "ch", "u", "v"], ["歌", "g", "e", "-"]]),
        new dat("兵仙神帅", [["兵", "b", "ing", "-"], ["仙", "x", "ian", "-"], ["神", "sh", "en", "/"], ["帅", "sh", "uai", "\\"]]),
        new dat("气吞山河", [["气", "q", "i", "\\"], ["吞", "t", "un", "-"], ["山", "sh", "an", "-"], ["河", "h", "e", "/"]]),
        new dat("借箸代筹", [["借", "j", "ie", "\\"], ["箸", "zh", "u", "\\"], ["代", "d", "ai", "\\"], ["筹", "ch", "ou", "/"]]),
        new dat("勋冠三杰", [["勋", "x", "un", "-"], ["冠", "g", "uan", "\\"], ["三", "s", "an", "-"], ["杰", "j", "ie", "/"]]),
        new dat("所向披靡", [["所", "s", "uo", "v"], ["向", "x", "iang", "\\"], ["披", "p", "i", "-"], ["靡", "m", "i", "v"]]),
        new dat("按甲休兵", [["按", "*", "an", "\\"], ["甲", "j", "ia", "v"], ["休", "x", "iu", "-"], ["兵", "b", "ing", "-"]]),
        new dat("楚河汉界", [["楚", "ch", "u", "v"], ["河", "h", "e", "/"], ["汉", "h", "an", "\\"], ["界", "j", "ie", "\\"]]),
        new dat("以一当十", [["以", "y", "i", "v"], ["一", "y", "i", "-"], ["当", "d", "ang", "-"], ["十", "sh", "i", "/"]]),
        new dat("暗渡陈仓", [["暗", "*", "an", "\\"], ["度", "d", "u", "\\"], ["陈", "ch", "en", "/"], ["仓", "c", "ang", "-"]]),
        new dat("妇人之仁", [["妇", "f", "u", "\\"], ["人", "r", "en", "/"], ["之", "zh", "i", "-"], ["仁", "r", "en", "/"]]),
        new dat("国士无双", [["国", "g", "uo", "/"], ["士", "sh", "i", "\\"], ["无", "w", "u", "/"], ["双", "sh", "uang", "-"]]),
        new dat("政由己出", [["政", "zh", "eng", "\\"], ["由", "y", "ou", "/"], ["己", "j", "i", "v"], ["出", "ch", "u", "-"]]),
        new dat("破釜沉舟", [["破", "p", "o", "\\"], ["釜", "f", "u", "v"], ["沉", "ch", "en", "/"], ["舟", "zh", "ou", "-"]]),
        new dat("一饭千金", [["一", "y", "i", "-"], ["饭", "f", "an", "\\"], ["千", "q", "ian", "-"], ["金", "j", "in", "-"]]),
        new dat("拔旗易帜", [["拔", "b", "a", "/"], ["旗", "q", "i", "/"], ["易", "y", "i", "\\"], ["帜", "zh", "i", "\\"]]),
        new dat("登坛拜将", [["登", "d", "eng", "-"], ["坛", "t", "an", "/"], ["拜", "b", "ai", "\\"], ["将", "j", "iang", "\\"]]),
        new dat("解衣推食", [["解", "j", "ie", "v"], ["衣", "y", "i", "-"], ["推", "t", "ui", "-"], ["食", "sh", "i", "/"]]),
        new dat("胯下之辱", [["胯", "k", "ua", "\\"], ["下", "x", "ia", "\\"], ["之", "zh", "i", "-"], ["辱", "r", "u", "v"]]),
        new dat("捷足先登", [["捷", "j", "ie", "/"], ["足", "z", "u", "/"], ["先", "x", "ian", "-"], ["登", "d", "eng", "-"]]),
        new dat("各自为战", [["各", "g", "e", "\\"], ["自", "z", "i", "\\"], ["为", "w", "ei", "/"], ["战", "zh", "an", "\\"]]),
        new dat("十面埋伏", [["十", "sh", "i", "/"], ["面", "m", "ian", "\\"], ["埋", "m", "ai", "/"], ["伏", "f", "u", "/"]]),
        new dat("劳苦功高", [["劳", "l", "ao", "/"], ["苦", "k", "u", "v"], ["功", "g", "ong", "-"], ["高", "g", "ao", "-"]]),
        new dat("江东父老", [["江", "j", "iang", "-"], ["东", "d", "ong", "-"], ["父", "f", "u", "\\"], ["老", "l", "ao", "v"]]),
        new dat("略不世出", [["略", "l", "ve", "\\"], ["不", "b", "u", "\\"], ["世", "sh", "i", "\\"], ["出", "ch", "u", "-"]]),
        new dat("披坚执锐", [["披", "p", "i", "-"], ["坚", "j", "ian", "-"], ["执", "zh", "i", "/"], ["锐", "r", "ui", "\\"]]),
        new dat("秋毫无犯", [["秋", "q", "iu", "-"], ["毫", "h", "ao", "/"], ["无", "w", "u", "/"], ["犯", "f", "an", "\\"]]),
        new dat("匹夫之勇", [["匹", "p", "i", "v"], ["夫", "f", "u", "-"], ["之", "zh", "i", "-"], ["勇", "y", "ong", "v"]]),
        new dat("略谙世事", [["略", "l", "ve", "\\"], ["谙", "*", "an", "-"], ["世", "sh", "i", "\\"], ["事", "sh", "i", "\\"]]),
        new dat("推陈出新", [["推", "t", "ui", "-"], ["陈", "ch", "en", "/"], ["出", "ch", "u", "-"], ["新", "x", "in", "-"]]),
        new dat("肝胆照人", [["肝", "g", "an", "-"], ["胆", "d", "an", "v"], ["照", "zh", "ao", "\\"], ["人", "r", "en", "/"]]),
        new dat("多多益善", [["多", "d", "uo", "-"], ["多", "d", "uo", "-"], ["益", "y", "i", "\\"], ["善", "sh", "an", "\\"]]),
        new dat("功高震主", [["功", "g", "ong", "-"], ["高", "g", "ao", "-"], ["震", "zh", "en", "\\"], ["主", "zh", "u", "v"]]),
        new dat("逐鹿中原", [["逐", "zh", "u", "/"], ["鹿", "l", "u", "\\"], ["中", "zh", "ong", "-"], ["原", "y", "uan", "/"]]),
        new dat("人心难测", [["人", "r", "en", "/"], ["心", "x", "in", "-"], ["难", "n", "an", "/"], ["测", "c", "e", "\\"]]),
        new dat("大失所望", [["大", "d", "a", "\\"], ["失", "sh", "i", "-"], ["所", "s", "uo", "v"], ["望", "w", "ang", "\\"]]),
        new dat("异军突起", [["异", "y", "i", "\\"], ["军", "j", "un", "-"], ["突", "t", "u", "-"], ["起", "q", "i", "v"]]),
        new dat("锦衣夜行", [["锦", "j", "in", "v"], ["衣", "y", "i", "-"], ["夜", "y", "e", "\\"], ["行", "x", "ing", "/"]]),
        new dat("独当一面", [["独", "d", "u", "/"], ["当", "d", "ang", "-"], ["一", "y", "i", "-"], ["面", "m", "ian", "\\"]]),
        new dat("霸王别姬", [["霸", "b", "a", "\\"], ["王", "w", "ang", "/"], ["别", "b", "ie", "/"], ["姬", "j", "i", "-"]]),
        new dat("金石之交", [["金", "j", "in", "-"], ["石", "sh", "i", "/"], ["之", "zh", "i", "-"], ["交", "j", "iao", "-"]]),
        new dat("力能扛鼎", [["力", "l", "i", "\\"], ["能", "n", "eng", "/"], ["扛", "g", "ang", "-"], ["鼎", "d", "ing", "v"]]),
        new dat("步步为战", [["步", "b", "u", "\\"], ["步", "b", "u", "\\"], ["为", "w", "ei", "/"], ["战", "zh", "an", "\\"]]),
        new dat("约法三章", [["约", "y", "ue", "-"], ["法", "f", "a", "v"], ["三", "s", "an", "-"], ["章", "zh", "ang", "-"]]),
        new dat("不赏之功", [["不", "b", "u", "\\"], ["赏", "sh", "ang", "v"], ["之", "zh", "i", "-"], ["功", "g", "ong", "-"]]),
        new dat("背水一战", [["背", "b", "ei", "\\"], ["水", "sh", "ui", "v"], ["一", "y", "i", "-"], ["战", "zh", "an", "\\"]]),
      ];
      const q = this.que;
      let result = [];
      for (let i of d) {
        let ok = true;
        for (let j of q) {
          if (j.id === -1) {
            let now = false;
            for (let k = 0; k < 4; k++) {
              for (let l = 0; l < 4; l++) {
                if (i.data[k][l] === j.s) {
                  now = true;
                  break;
                }
              }
              if (now) break;
            }
            if (!now) {
              ok = false;
              break;
            }
          } else {
            let now = false;
            for (let k = 0; k < 4; k++) {
              if (i.data[j.id][k] === j.s) {
                now = true;
                break;
              }
            }
            if (!now) {
              ok = false;
              break;
            }
          }
        }
        if (ok) result.push({val: i.word});
      }
      this.result = result;
    }
  },
  data() {
    return {
      input: '',
      result: Array(),
      query: Array(),
      que: Array()
    }
  }
}
</script>

<style>
.el-header {
  text-align: center;
  background-color: #81e7ff;
  line-height: 100px;
}

.el-aside {
  text-align: center;
  background-color: #a9eeff;
  height: 100%;
  padding: 15px;
}

.el-main {
  text-align: center;
  background-color: #c6f7ff;
}

.el-table {
  text-align: center;
  background-color: lightcyan;
  height: 100%;
}

span {
  width: 100% !important;
  float: left !important;
  overflow: hidden !important;
  text-overflow: ellipsis !important;
  white-space: normal !important;
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}
</style>